import styles from './search_header.module.css';
import React, { useRef } from 'react';

const SearchHeader = ({onSearch}) => {

  const inputRef = useRef();

  const handleSearch = ()=>{
    const value = inputRef.current.value;
    console.log(value);
    onSearch(value);
  };

  const onClick = (e)=>{
    handleSearch();
  };

  const onKeyPress = (e)=>{
    if(e.key === 'Enter'){
      handleSearch();
    }
  };

  return (
    <header className={styles.header}>

      <div className={styles.logo}>
      <img className={styles.img} src="/images/logo.png" alt="logo" />
      <h1 className={styles.title}>Youtube</h1>
      </div>

      <input ref={inputRef} className={styles.input} type="search" placeholder="search" onKeyPress={(e)=>{onKeyPress(e)}}/>
      <button type="submit" className={styles.button} onClick={(e)=>{onClick(e)}}>
        <img className={styles.buttonImg} src="/images/search.png" alt="" />
      </button>
    </header>
  )
}

export default SearchHeader;